import React, { FC, useState } from "react";
import { ProfileItem } from "../models/ProfileItem";
import Accordion from "@mui/material/Accordion";
import AccordionDetails from "@mui/material/AccordionDetails";
import AccordionSummary from "@mui/material/AccordionSummary";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Button,
  Chip,
  Divider,
  List,
  ListItem,
  ListItemText
} from "@mui/material";
import { ProfileMeta } from "../models/ProfileMeta";
import EventAvailableOutlinedIcon from "@mui/icons-material/EventAvailableOutlined";
import HomeWorkIcon from "@mui/icons-material/HomeWork";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import AccountCircleOutlinedIcon from "@mui/icons-material/AccountCircleOutlined";

interface ProfileListProps {
  dataList: ProfileMeta[];
}

export const ProfileListComponent: FC<ProfileListProps> = ({ dataList }) => {
  const [expanded, setExpanded] = useState<string | false>(false);

  const handleChange =
    (panel: string) => (event: React.SyntheticEvent, isExpanded: boolean) => {
      setExpanded(isExpanded ? panel : false);
    };

  const populateList = (index: number, profileItem: ProfileItem) => {
    return (
      <>
        <Typography
          sx={{ width: "100%", flexShrink: 0 }}
          style={{ fontWeight: "bold" }}
        >
          {profileItem.entityType === "Person" && (
            <>
              <AccountCircleOutlinedIcon />
              {profileItem.name}
            </>
          )}
          {profileItem.entityType === "Building" && (
            <>
              <HomeWorkIcon />
              {profileItem.name}
            </>
          )}
          {profileItem.entityType === "Whatsapp" && (
            <>
            
              <img src="social.png" id="social" style={{ width: "30px" }}></img>
              <a href={profileItem.link} target="_blank">
                {profileItem.name}
              </a>
            </>
          )}
        </Typography>
        {profileItem.entityType != "Whatsapp" && (
          <>
            <ListItem
              alignItems="flex-start"
              style={{ paddingTop: "0px", paddingBottom: "0px" }}
            >
              <ListItemText
                primary={<React.Fragment>{profileItem.address}</React.Fragment>}
                secondary={
                  <React.Fragment>
                    {profileItem.time && (
                      <Typography
                        sx={{ display: "inline" }}
                        component="span"
                        variant="body2"
                        color="text.primary"
                      >
                        <Chip
                          icon={<EventAvailableOutlinedIcon />}
                          label={profileItem.time}
                          variant="outlined"
                        />
                      </Typography>
                    )}
                  </React.Fragment>
                }
              />
            </ListItem>
            <ListItem alignItems="flex-start">
              <ListItemText
                primary={
                  <React.Fragment>
                    <a href={`tel:${profileItem.contact}`}>
                      <Button
                        style={{
                          marginBottom: "2px",
                          fontWeight: "bold",
                          color: "#000"
                        }}
                      >
                        <LocalPhoneIcon color="primary" /> {profileItem.contact}
                      </Button>
                    </a>
                  </React.Fragment>
                }
              />
            </ListItem>
            <Divider variant="fullWidth" component="li" />
          </>
        )}
      </>
    );
  };

  const populateItems = (index: number, profileMeta: ProfileMeta) => {
    return (
      <Accordion
        expanded={expanded === `panel${index}`}
        onChange={handleChange(`panel${index}`)}
        key={index}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
        >
          <Typography
            sx={{ width: "33%", flexShrink: 0 }}
            style={{ fontWeight: "bold" }}
          >
            {profileMeta.key.toUpperCase()}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          {/* <Typography>{JSON.stringify(profileItem)}</Typography> */}
          {profileMeta.profileItemList.map((item, index) =>
            populateList(index, item)
          )}
        </AccordionDetails>
      </Accordion>
    );
  };

  return (
    <>
      <List sx={{ width: "100%", maxWidth: 360, bgcolor: "background.paper" }}>
        {dataList.map((item, index) => populateItems(index, item))}
      </List>

      {/* <Accordion
        expanded={expanded === "panel2"}
        onChange={handleChange("panel2")}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
        >
          <Typography sx={{ width: "33%", flexShrink: 0 }}>Users</Typography>
          <Typography sx={{ color: "text.secondary" }}>
            You are currently not an owner
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Donec placerat, lectus sed mattis semper, neque lectus feugiat
            lectus, varius pulvinar diam eros in elit. Pellentesque convallis
            laoreet laoreet.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel3"}
        onChange={handleChange("panel3")}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3bh-content"
          id="panel3bh-header"
        >
          <Typography sx={{ width: "33%", flexShrink: 0 }}>
            Advanced settings
          </Typography>
          <Typography sx={{ color: "text.secondary" }}>
            Filtering has been entirely disabled for whole web server
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Nunc vitae orci ultricies, auctor nunc in, volutpat nisl. Integer
            sit amet egestas eros, vitae egestas augue. Duis vel est augue.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion
        expanded={expanded === "panel4"}
        onChange={handleChange("panel4")}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel4bh-content"
          id="panel4bh-header"
        >
          <Typography sx={{ width: "33%", flexShrink: 0 }}>
            Personal data
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Nunc vitae orci ultricies, auctor nunc in, volutpat nisl. Integer
            sit amet egestas eros, vitae egestas augue. Duis vel est augue.
          </Typography>
        </AccordionDetails>
      </Accordion> */}
    </>
  );
};
