import AutoAwesomeRoundedIcon from "@mui/icons-material/AutoAwesomeRounded";
import { PaletteMode } from "@mui/material";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import ToggleButton from "@mui/material/ToggleButton";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import * as React from "react";
import CustomAppBar from "./components/CustomAppBar";
import { ChatBot } from "./pages/ChatBot";
import getLPTheme from "./getLPTheme";
import { CategoryMenuComponent } from "./components/CategoryMenuComponent";
import { fetchData } from "./services/instagram.service";
import { SpeedDialComponent } from "./components/SpeedDialComponent";

interface ToggleCustomThemeProps {
  showCustomTheme: Boolean;
  toggleCustomTheme: () => void;
}

function ToggleCustomTheme({
  showCustomTheme,
  toggleCustomTheme
}: ToggleCustomThemeProps) {
  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "100dvw",
        position: "fixed",
        bottom: 24
      }}
    >
      <ToggleButtonGroup
        color="primary"
        exclusive
        value={showCustomTheme}
        onChange={toggleCustomTheme}
        aria-label="Platform"
        sx={{
          backgroundColor: "background.default",
          "& .Mui-selected": {
            pointerEvents: "none"
          }
        }}
      >
        <ToggleButton value>
          <AutoAwesomeRoundedIcon sx={{ fontSize: "20px", mr: 1 }} />
          Custom theme
        </ToggleButton>
        <ToggleButton value={false}>Material Design 2</ToggleButton>
      </ToggleButtonGroup>
    </Box>
  );
}

export default function LandingPage() {
  const [mode, setMode] = React.useState<PaletteMode>("light");
  const [showCustomTheme, setShowCustomTheme] = React.useState(false);
  const LPtheme = createTheme(getLPTheme(mode));
  const defaultTheme = createTheme({ palette: { mode } });
  const [showPage3, setShowPage3] = React.useState(false);
  const [showChatBot, setShowChatBot] = React.useState(true);
  const [page3Posts, setPage3Posts] = React.useState([]);

  const toggleColorMode = () => {
    setMode((prev) => (prev === "dark" ? "light" : "dark"));
  };
  const [title, setTitle] = React.useState("");

  const selectedCategory = async (category: any) => {
    console.log("selectedCategory :::: ", category);
    setTitle(category);
    if (category === "Page 3" || category === "Event") {
      setShowChatBot(false);
      setShowPage3(true);
      // let data = await fetchData("nikoohomes");
      // console.log("data :: ", data);
    }
    if (category === "Category") {
      setShowChatBot(false);
      setShowPage3(true);
      // let data = await fetchData("nikoohomes");
      // console.log("data :: ", data);
    }
  };

  const closePage3 = () => {
    setShowPage3(false);
    setShowChatBot(true);
  };

  React.useEffect(() => {
    console.log("....");
  }, [showPage3]);

  const toggleCustomTheme = () => {
    setShowCustomTheme((prev) => !prev);
  };

  return (
    <>
      <ThemeProvider theme={showCustomTheme ? LPtheme : defaultTheme}>
        <CssBaseline />
        <CustomAppBar
          mode={mode}
          toggleColorMode={toggleColorMode}
          selectedCategory={selectedCategory}
        />
        {showChatBot && <ChatBot></ChatBot>}

        {showPage3 && (
          <CategoryMenuComponent
            data={page3Posts}
            show={showPage3}
            closePage3={closePage3}
            title={title}
          ></CategoryMenuComponent>
        )}
      </ThemeProvider>
      <SpeedDialComponent
        selectedCategory={selectedCategory}
      ></SpeedDialComponent>
    </>
  );
}
