import {
  Button,
  Card,
  CardContent,
  Chip,
  Grid,
  Typography
} from "@mui/material";
import { FC } from "react";
import HomeWorkIcon from "@mui/icons-material/HomeWork";
import LocalPhoneIcon from "@mui/icons-material/LocalPhone";
import EventAvailableOutlinedIcon from "@mui/icons-material/EventAvailableOutlined";
import AccountCircleOutlinedIcon from "@mui/icons-material/AccountCircleOutlined";
import SubwayOutlinedIcon from "@mui/icons-material/SubwayOutlined";
import { WhatsApp } from "../../models/WhatsApp";

interface WhatsAppTypeComponentProps {
  whatsAppItem: WhatsApp;
}

export const WhatsAppTypeComponent: FC<WhatsAppTypeComponentProps> = ({
  whatsAppItem
}) => {
  return (
    <>
      <Card
        sx={{
          width: "100%",
          padding: "1%",
          paddingTop: "0px",
          paddingBottom: "0px"
        }}
      >
        <CardContent>
          <Grid container spacing={2} columns={10}>
            <Grid item xs={2}>
              <img src="social.png" id="social" style={{ width: "30px" }}></img>
            </Grid>
            <Grid item xs={8}>
              <Typography
                sx={{ fontSize: 14 }}
                color="text.secondary"
                gutterBottom
                style={{ fontWeight: "bold" }}
              >
                <a href={whatsAppItem.link} target="_blank">
                  <span style={{ textAlign: "center" }}>
                    {whatsAppItem.title}
                  </span>
                </a>
              </Typography>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
      <br />
    </>
  );
};
