import React, { FC, useEffect, useState } from "react";
import { Global } from "@emotion/react";
import { styled } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import { grey, pink } from "@mui/material/colors";
import LocalMoviesOutlinedIcon from "@mui/icons-material/LocalMoviesOutlined";
import Typography from "@mui/material/Typography";
import SwipeableDrawer from "@mui/material/SwipeableDrawer";
import CloseIcon from "@mui/icons-material/Close";
import Button from "@mui/material/Button";
import { Image } from "@mui/icons-material";
import { Grid } from "@mui/material";
import { getProfileServices } from "../services/profile.service";
import { ProfileGridComponent } from "./ProfileGridComponent";
import { Profile } from "../models/Profile";
import { profile } from "console";
import { ProfileItem } from "../models/ProfileItem";
import { ProfileListComponent } from "./ProfileListComponent";
import { ProfileMeta } from "../models/ProfileMeta";
import UndoOutlinedIcon from "@mui/icons-material/UndoOutlined";

const drawerBleeding = 56;
interface CategoryMenuProps {
  window?: () => Window;
  show: boolean;
  data: any;
  closePage3: () => void;
  title: string;
}

const StyledBox = styled("div")(({ theme }) => ({
  backgroundColor: theme.palette.mode === "light" ? "#fff" : grey[800]
}));

const Puller = styled("div")(({ theme }) => ({
  width: 30,
  height: 6,
  backgroundColor: theme.palette.mode === "light" ? grey[300] : grey[900],
  borderRadius: 3,
  position: "absolute",
  top: 8,
  left: "calc(50% - 15px)"
}));

export const CategoryMenuComponent: FC<CategoryMenuProps> = ({
  window,
  show,
  closePage3,
  title
}) => {
  const [open, setOpen] = React.useState(show);

  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [profileContent, setProfileContent] = useState(false);
  const [profileList, setProfileList] = useState<ProfileMeta[]>([]);

  const toggleDrawer = (newOpen: boolean) => () => {
    setOpen(newOpen);
  };

  const handleClose = () => {
    closePage3();
  };

  const handleBack = () => {
    setProfileContent(false);
  };

  //let profileList: Profile[] = [];
  const openCategory = async (category: string) => {
    console.log("parent inside open", category);
    let profileMeta: ProfileMeta[] = await getProfileServices(category);
    console.log("parent inside open: profiledata", profileMeta);
    setProfileList(profileMeta);
    //profileList = profiledata;
    setProfileContent(true);
  };

  const populateItems = (index: number, data: any) => {
    return (
      <>
        <span key={index}>{data["name"]}</span>
        <br />
      </>
    );
  };
  const container =
    window !== undefined ? () => window().document.body : undefined;

  return (
    <>
      {open && (
        <>
          <CssBaseline />
          <Global
            styles={{
              ".MuiDrawer-root > .MuiPaper-root": {
                height: `calc(85% - ${drawerBleeding}px)`,
                overflow: "visible"
              }
            }}
          />
          <SwipeableDrawer
            container={container}
            anchor="bottom"
            open={open}
            onClose={toggleDrawer(false)}
            onOpen={toggleDrawer(true)}
            swipeAreaWidth={drawerBleeding}
            disableSwipeToOpen={false}
            ModalProps={{
              keepMounted: true
            }}
          >
            <StyledBox
              sx={{
                position: "absolute",
                top: -drawerBleeding,
                borderTopLeftRadius: 8,
                borderTopRightRadius: 8,
                visibility: "visible",
                right: 0,
                left: 0
              }}
            >
              <Puller />
              <Typography sx={{ p: 2, color: pink[500], width: "100%" }}>
                <LocalMoviesOutlinedIcon />{" "}
                <span style={{ fontSize: "20px" }}>{title}</span>
                {profileContent && (
                  <>
                    <UndoOutlinedIcon
                      style={{ marginLeft: "160px", paddingTop: "2px" }}
                      onClick={handleBack}
                    ></UndoOutlinedIcon>
                    <CloseIcon
                      style={{ paddingTop: "2px" }}
                      onClick={handleClose}
                    ></CloseIcon>
                  </>
                )}
                {!profileContent && (
                  <>
                    <CloseIcon
                      style={{ marginLeft: "190px", paddingTop: "2px" }}
                      onClick={handleClose}
                    ></CloseIcon>
                  </>
                )}
              </Typography>
            </StyledBox>

            {profileList && profileList.length > 0 && profileContent && (
              <StyledBox
                sx={{
                  px: 2,
                  pb: 2,
                  height: "100%",
                  overflow: "auto",
                  backgroundColor: "white",
                  marginTop: "30px",
                  paddingLeft: "15px"
                }}
              >
                <ProfileListComponent
                  dataList={profileList}
                ></ProfileListComponent>
                {/* {profileList.map((item, index) => populateItems(index, item))} */}
              </StyledBox>
            )}
            {!profileContent && (
              <StyledBox
                sx={{
                  px: 2,
                  pb: 2,
                  height: "100%",
                  overflow: "auto",
                  backgroundColor: "white",
                  marginTop: "30px",
                  paddingLeft: "50px"
                }}
              >
                <ProfileGridComponent
                  openCategory={openCategory}
                ></ProfileGridComponent>
              </StyledBox>
            )}
          </SwipeableDrawer>
        </>
      )}
    </>
  );
};
